.body {
  background-color: #eeeeee;
}
.index-container {

}
.tabbar-container {
  height:100%;
}
.under-container {
  margin-top:20px;
  text-align:center;
}

.under-container img {
  margin-left:3%;
  width:30%;
  display:inline-block;
}
.under-container img:nth-child(1) {
  margin-left:0px;
}
.hot-advice {
  background-color: white;
}
.hot-advice-title {
  width:100%;
  background-color: white;
  height:45px;
}
.hot-advice-title h2 {
  line-height:45px;
  padding-left:30px;
}
.hot-advice-img-container {
  height:200px;
  overflow: hidden;
  text-align:center;
  position:relative;
  text-align:center;
}
.hot-advice-img-container img {
  width:100%;
  position:absolute;
  left:0%;
  top:-10%;
  z-index:1;
  /* height:40px; */
}
.hot-advice-img-container span {
  z-index:3;
  position:absolute;
  top:80%;
  color:white;
  width:90%;
  left:5%;
  font-size:15px;
}

.method-nav {
  background-color: #3fcccb;
}

.method-nav span {
  color:white;
}

.item-container {
  width:100%;
}
.show-item {
  display:inline-block;
  width:40%;
  margin-left:6.5%;
  margin-top:6.5vw;
  float:left;
  overflow:hidden;
  position:relative;
  border-radius:8px;
}
.show-item img {
  width:100%;
}
.item-content {
  position:absolute;
  width:100%;
  height:15%;
  background-color: white;
  top:85%;

}
.item-content img {
  width:8%;
  position:absolute;
  top:-5px;
  left:10px;
}

.item-content span {
  font-size:12px;
  position:absolute;
  top:20%;
  left:70%;
  color:#aaaaaa;
}
#img2 {
  left:90%;
  top:15%;
}

.icon-sell-container {
  width:100%;
  margin-top:10px;

}
.icon-sell-item {
  width:15%;
  height:15vw;
  border-radius:15vw;
  background-color: pink;
  margin-left:5%;
  display:inline-block;
  text-align:center;
  margin-top:15px;

}
.icon-sell-container span {
  line-height:15vw;
  font-size:4vw;
  color:white;
}


.onsell {
  width:35%;
  height:30vw;
  background-color: white;
  margin-top:20px;
  text-align:center;
  margin-left:10%;
  display:inline-block;
  float:left;

}
.onsell img {
  padding-top:10px;
  width:90%;

}
.onsell:last-child img {
  width:70%;
}
.onsell img:first-child {
  padding-top:10px;
}
.onsell span {
  display:block;
  margin-top:10px;
}
